* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(202, 158, 253);
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1000px;
  margin: 50px auto;
  padding: 50px;

  background-color: #fff;
  border-radius: 80px;
  box-shadow: 0 0 15px rgba(75, 74, 74, 0.3);
}

.App__title {
  color: rgb(173, 145, 206);
}
